@import url('https://fonts.googleapis.com/css2?family=Nunito&family=Poppins:wght@200&display=swap');

*{
    font-family:  'Nunito', sans-serif;
}
.login-btn{
    border: 1px solid rgb(0, 173, 189);
    color: rgb(0, 173, 189);
}
.login-btn:hover{
    background-color: rgb(37, 37, 37);
    color: rgb(0, 173, 189);
}
.sign-up-btn{
    background-color: rgb(0, 173, 189);
    color: black;
}
.sign-up-btn:hover{
    background-color: rgb(37, 37, 37);
    color: rgb(0, 173, 189);
}
#carouselExample{
    height: 100%;
}
.images{
    height: 500px;
}
.cards{
    margin: auto;
}
.addCart{
    background-color: rgb(0, 173, 189);
}
.addCart:hover{
    background-color: rgb(37, 37, 37);
    color: rgb(0, 173, 189);  
}
footer{
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    padding: 20px;
    background-color: rgb(214, 214, 214);
}
.footer-content:last-child{
    grid-column: 1/5;
    text-align: center;
}
.menus{
    text-decoration: none;
    color: rgb(12, 12, 12);
}
.message-btn{
    background-color: rgb(0, 173, 189);
    padding: 10px;
    border-radius: 10px;
    color: black;
    text-decoration: none;
}
.message-btn:hover{
    background-color: rgb(37, 37, 37);
    color: rgb(0, 173, 189); 
}